.example {
  padding-top: 20px;
  padding-bottom: 30px;

  background: $grey;
}

.example__section-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.example__description-wrapper {
  width: 100%;
}

.example__title {
  margin: 0;
  margin-bottom: 29px;
}

.example__text {
  width: 266px;
  margin: 0;
  margin-bottom: 23px;

  color: $brown;

  font-family: "ArialMT", "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
}

.example__price-wrapper {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;

  width: 100%;
  margin-bottom: 38px;
}

.example__result-wrapper {
  display: flex;
  justify-content: space-between;

  width: 100%;
}

.example__result {
  position: relative;

  width: 124px;
  padding: 15px;

  border: 1px solid $grey2;
  border-radius: 5px;
}

.example__price {
  align-self: center;

  width: 100%;
  margin: 0;
  margin-top: 35px;

  text-align: center;
  text-transform: uppercase;

  font-size: 14px;
  line-height: 20px;
}

.example__value {
  display: block;

  text-transform: uppercase;
  text-align: center;

  font-size: 24px;
  line-height: 24px;
}

.example__field {
  position: absolute;
  top: 47px;
  left: 17px;

  padding: 0 8px 0 8px;
  width: 87px;

  text-align: center;

  color: $brown;
  background: $grey;

  font-family: "ArialMT", "Arial", sans-serif;
  font-size: 12px;
  line-height: 12px;
}

.example__image-wrapper {
  width: 100%;
  margin-bottom: 25px;

  &--show {
    .example__image--after {
      display: block;
    }

    .example__image--before {
      display: none;
    }
  }
}


.example__image {
  width: 320px;
  height: 240px;

  &--before {
    background: url("../img/before-mobile1x.png") no-repeat;
    background-size: contain;
    background-position: -19px -5px;

    @media (min-resolution: $retina-dppx) {
      background-image: url("../img/before-mobile2x.png");
    }
  }

  &--after {
    background: url("../img/after-mobile1x.png") no-repeat;
    background-size: contain;
    display: none;

    @media (min-resolution: $retina-dppx) {
      background-image: url("../img/after-mobile2x.png");
    }
  }
}

.example__demo {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;

  padding-bottom: 3px;
  overflow: hidden;

  width: 100%;
}

.example__button-wrapper {
  display: flex;
  justify-content: space-around;

  width: 220px;

  text-transform: uppercase;
  cursor: pointer;

  font-size: 16px;
  line-height: 20px;
}

.example__before,
.example__after {
  width: 50px;
  padding: 0;

  border: none;
  text-transform: uppercase;

  background: transparent;

  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 1.5;
}


.example__track {
  position: relative;

  align-self: center;

  width: 84px;
  height: 14px;

  border-radius: 6px;
  border: 1px solid $grey2;

  background: $white;
}

.example__cursor {
  &::after {
    content: "";

    position: absolute;
    top: 3px;
    left: 6px;

    width: 35px;
    height: 6px;

    border-radius: 3px;

    background: $green;
  }
}

@media (min-width: $tablet-width) {
  .example {
    position: relative;

    padding-bottom: 66px;
    padding-top: 27px;

    border-top: 1px solid $grey2;

    background: linear-gradient(to bottom, $white 0, $white 618px, $grey 618px, $grey 100%);
  }

  .example__description-wrapper {
    width: 100%;
  }

  .example__title {
    margin: 0;
    margin-bottom: 50px;
  }

  .example__text {
    width: 678px;
    margin-bottom: 53px;

    font-size: 16px;
    line-height: 24px;
  }

  .example__price-wrapper {
    width: 100%;
    margin-bottom: 86px;
  }

  .example__result-wrapper {
    width: 366px;
  }

  .example__result {
    width: 162px;
    padding: 20px;
  }

  .example__price {
    align-self: center;

    width: 174px;
    margin: 0;

    text-align: right;

    font-size: 20px;
    line-height: 20px;
  }

  .example__price-value {
    display: inline-block;

    margin-top: 9px;
  }

  .example__image-wrapper {
    width: 100%;
    margin-bottom: 15px;
  }


  .example__button-wrapper {
    width: 596px;
  }


  .example__value {
    width: 100%;

    font-size: 30px;
    line-height: 1;
  }

  .example__field {
    top: 77px;
    left: 10px;

    background: $white;

    font-size: 14px;
    line-height: 14px;

    &--weight {
      top: 63px;
      left: 22px;

      width: 115px;
    }

    &--time {
      top: 63px;
      left: 8px;

      width: 143px;
    }
  }

  .example__demo {
    width: 100%;

    font-size: 20px;
  }

  .example__image {
    width: 690px;
    height: 517px;
    margin: 0 auto;

    &--before {
      background: url("../img/before-tablet1x.png") no-repeat;
      background-size: contain;
      background-position: 7px 0;

      @media (min-resolution: $retina-dppx) {
        background-image: url("../img/before-tablet2x.png");
      }
    }

    &--after {
      background: url("../img/after-tablet1x.png") no-repeat;
      background-size: contain;
      display: none;

      @media (min-resolution: $retina-dppx) {
        background-image: url("../img/after-tablet2x.png");
      }
    }
  }

  .example__before,
  .example__after {
    width: 50px;
    padding: 0;

    border-bottom: 1px dotted $grey2;

    font-size: 20px;
  }

  .example__cursor {
    position: absolute;
    top: -14px;
    left: 198px;

    width: 34px;
    height: 34px;

    cursor: pointer;

    border: 1px solid $grey2;
    border-radius: 50%;

    background: $white;

    &:hover {
      box-shadow: 0 3px 5px rgba(0, 0, 0, 0.25);
    }

    &:focus {
      border: 2px solid $green;
      box-shadow: 0 4px 5px rgba(0, 0, 0, 0.3);
    }


    &::after {
      content: "";

      position: absolute;
      top: 12px;
      left: 12px;

      width: 8px;
      height: 8px;

      border-radius: 50%;

      background: $green;
    }
  }

  .example__track {
    width: 428px;
    height: 6px;

    border: none;
    border-radius: 2.5px;

    background: $grey3;
  }
}

@media (min-width: $desktop-width) {
  .example {
    padding: 0;
    padding-bottom: 66px;

    border: none;

    background: linear-gradient(to bottom, $white 0, $white 212px, $grey 212px, $grey 100%);
  }

  .example__section-wrapper {
    flex-wrap: nowrap;
  }

  .example__title {
    width: 100%;
    margin: 0;
    margin-bottom: 132px;
  }

  .example__text {
    width: 432px;
    margin-bottom: 68px;
  }

  .example__description-wrapper {
    width: 614px;
    padding-top: 62px;
  }

  .example__demo {
    width: 690px;
  }

  .example__result-wrapper {
    width: 367px;
    margin: 0;
  }

  .example__result {
    padding: 17px 29px 21px 29px;
  }

  .example__price-wrapper {
    margin: 0;
  }

  .example__price {
    width: 435px;
    margin-top: 53px;

    text-align: left;
  }

  .example__price-value {
    margin-left: 59px;
  }

  .example__field {
    background: $grey;
  }

  .example__image-wrapper {
    min-height: 517px;
  }

  .example__image {
    background-position-x: -6px;
    background-position-y: 21px;
    background-size: 690px;
  }

  .example__button-wrapper {
    justify-content: space-between;

    width: 575px;
    margin-top: 41px;
    margin-left: 74px;
  }

  .example__wrapper {
    width: 50%;
  }
}
